<template>
  <div class="category">
    <!-- 搜索框 -->
    <SearchInput></SearchInput>

    <div class="contain_wrap">
      <Tabs :tabs="tabs" @exTabItem="exTabItem">
        <div v-if="tabs[tabitem].isActive===true">
          <div class="first_tabs">
            <router-link
              class="tab-item"
              v-for="item in goodsList"
              :key="item.goods_id"
              :to="'/goodsdetail?goods_id='+item.goods_id"
            >
              <div class="left">
                <van-image
                  :src="item.goods_small_logo?item.goods_small_logo:'https://ww1.sinaimg.cn/large/007rAy9hgy1g24by9t530j30i20i2glm.jpg'"
                ></van-image>
              </div>
              <div class="right">
                <div class="r_top">{{item.goods_name}}</div>
                <div class="r_bottom">￥{{item.goods_price}}</div>
              </div>
            </router-link>
          </div>
        </div>
      </Tabs>
    </div>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import Tabs from "../components/Tabs";
import SearchInput from "../components/SearchInput";
export default {
  data() {
    return {
      tabs: [
        {
          id: 0,
          value: "销量",
          isActive: true
        },
        {
          id: 1,
          value: "综合",
          isActive: false
        },
        {
          id: 2,
          value: "价格",
          isActive: false
        }
      ],
      //   商品数据
      goodsList: [],
      // 总页数
      totalPages: 1,
      tabitem: 0
    };
  },

  created() {
    this.getData();
  },
  methods: {
    /* 获取数据 */
    getData() {
      /* 接口参数 */
      let getParams = {
        query: "",
        cid: "",
        pagenum: 1,
        pagesize: 10
      };
      // 分类总数据
      reaxios(0, { url: "/goods/search", getParams }).then(result => {
        /* 
          计算总页数
          总条数 total
          页容量 this.getParams.pagesize 
        */
        this.goodsList = result.data.message.goods;
        const total = this.goodsList.length;
        const pagesize = getParams.pagesize;
        this.totalPages = Math.ceil(total / pagesize);
      });
    },
    exTabItem(e) {
      this.tabitem = e;
    }
  },

  // 自定义组件注册
  components: { Tabs, SearchInput }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  padding: 50px 0;
  .first_tabs {
    .tab-item {
      display: flex;
      padding: 5px;
      border-bottom: 1px solid var(--threeColor);
      .left {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;

        .van-image {
          width: 70%;
        }
      }
      .right {
        flex: 2;
        display: flex;
        flex-direction: column; //改变主轴方向
        justify-content: space-around;
        .r_top {
          text-align: left;

          display: -webkit-box;
          overflow: hidden;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          color: var(--secondColor);
        }
        .r_bottom {
          text-align: right;
          font-size: 18px;
          color: var(--themeColor);
        }
      }
    }
  }
}
</style>


