<template>
  <div class="tabs-box">
    <div class="tabs-titel">
      <div
        class="tabs-item"
        @click="exTab(index)"
        :class="[tabs-item,item.isActive?'active':' ']"
        v-for="(item,index) in tabs"
        :key="item.id"
      >{{item.value}}</div>
    </div>
    <div class="tabs-contain">
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tabs: {
      type: Array
    }
  },

  methods: {
    exTab(index) {
      this.tabs.forEach(v => {
        v.isActive = false;
      });
      this.tabs[index].isActive = true;
      this.$emit("exTabItem", index);
    }
  }
};
</script>

<style lang="less" scoped>
.tabs-box {
  width: 100%;
}
.tabs-titel {
  display: flex;
  text-align: center;
  justify-content: space-around;

  .tabs-item {
    flex: 1;
    border-left: 2px solid rgba(206, 206, 206, 0.5);
    border-bottom: 2px solid rgba(206, 206, 206, 0.5);
    color: var(--secondColor);
    padding: 10px 0;

    &:first-child {
      border-left: none;
    }
  }
  .active {
    color: var(--themeColor);
  }
}
</style>